.btn-style {
  background-color: #404040;
  border: none;
  color: #91adf6;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  width: 230px;
  height: 50px;
  font-family: "Volkhov";
  margin-left: 20px;
}



