.container-input {
  width: 500px;
  height: 50px;
  margin-bottom: 40px;
  font-family: "Volkhov";
}

.input-style {
  background-color: #91adf6;
  border-color: #91adf6;
  padding: 5px;
  border-radius: 5px;
  width: 300px;
  height: 50px;
  float: right;
  background-repeat: no-repeat;
  background-position-x: 10px;
  background-position-y: center;
  padding-left: 40px;
}

.input-style::-webkit-input-placeholder {
  color: #1F212170;
}

.input-style:-ms-input-placeholder {
  color: #1F212170;
}

.input-style::-ms-input-placeholder {
  color: #1F212170;
}

.input-style::placeholder {
  color: #1F212170;
}

.label-style {
  font-size: 16px;
  margin-bottom: 20px;
  height: 50px;
}

.username-img {
  background-image: url(/assets/imgs/username.png);
  background-size: 20px;
}

.password-img {
  background-image: url(/assets/imgs/password.png);
  background-size: 20px;
}

.calender-img {
  background-image: url(/assets/imgs/calender.svg);
  background-size: 20px;
}

.plane-img {
  background-image: url(/assets/imgs/plane.png);
  background-size: 25px;
}

.departuretime-img {
  background-image: url(/assets/imgs/departuretime.svg);
  background-size: 30px;
}

.gate-img {
  background-image: url(/assets/imgs/gate.png);
  background-size: 25px;
}

.terminalno-img {
  background-image: url(/assets/imgs/terminalno.svg);
  background-size: 30px;
}

.time-img {
  background-image: url(/assets/imgs/time.png);
  background-size: 17px;
}



